document.addEventListener("DOMContentLoaded", function() {
    let message = `/*å¤§å®¶å¥½ï¼Œæˆ‘æ˜¯å®‰å®‰ï¼Œè¿™
* æ˜¯æˆ‘çš„ç¬¬ä¸€ä¸ªjavascriptå°æ¡ˆä¾‹ï¼šåˆ¶ä½œä¸€ä¸ªå¤ªæ
* é¦–å…ˆï¼Œå‡†å¤‡ä¸€ä¸ªDIV
* ç„¶åæ·»åŠ æ ·å¼
*/
#content {
    width: 200px;
    height: 200px;
    box-shadow: 0 0 3px rgba(0, 0, 0, .4);
}
/*ç„¶åå°†DIVå˜æˆä¸€ä¸ªåœ†å½¢*/
#content {
    border-radius: 50%;
}
/*å¤ªæåˆ†é˜´é˜³
* æ··æ²Œåˆå¼€
* é˜´é˜³ä¸¤ä»ª
*/
#content {
    background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 50%, rgba(0,0,0,1) 50%, rgba(0,0,0,1) 100%);
}
/*åˆ†è€Œä¸ºäºŒä»¥è±¡ä¸¤
* æ˜“æœ‰å¤ªæï¼Œé˜´é˜³äºŒçˆ»
*/
#content::before,
#content::after {
    width: 100px;
    height: 100px;
    border: 1px solid red;
    border-radius: 50%
}
#content::after {
    background-color: #fff;
    border: 0;
}
#content::before {
    background-color: #000;
    border:0;
}
/*é˜´é˜³ç›¸æ±‡
* ç”Ÿç”Ÿä¸æ¯
*/
#content::before {
    background: radial-gradient(circle, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 25%, rgba(0,0,0,1) 27%, rgba(0,0,0,1) 100%);
}
#content::after {
    background: radial-gradient(circle, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 25%, rgba(255,255,255,1) 27%, rgba(255,255,255,1) 100%);
}
/*
* é“ç”Ÿä¸€
* ä¸€ç”ŸäºŒ
* äºŒç”Ÿä¸‰
* ä¸‰ç”Ÿä¸‡ç‰©
*/
#container {
    animation: rotate 3s linear infinite;
}
@keyframes rotate{from{transform: rotate(0deg)}
to{transform: rotate(-360deg)}
`
    let newMessage = '';
    let container = document.getElementById('container');
    let content = document.getElementById('content');
    let operation = document.getElementById('operation');
    let style = document.getElementById('style');

    let index = 0;

    function step() {

        setTimeout(() => {
            if (message[index] === '\n') {
                newMessage += '<br>'
            } else if (message[index] === ' ') {
                newMessage += '&nbsp;'
            } else {
                newMessage += message[index]
            }
            operation.innerHTML = newMessage;
            // cssæ·»åŠ ä¸èƒ½æ›¿æ¢å›è½¦å’Œæ¢è¡Œï¼Œç”¨å­—ç¬¦ä¸²æˆªå–çš„æ–¹å¼æ·»åŠ 
            style.innerHTML = message.substring(0, index);
            operation.scrollTo(0, 99999);
            window.scrollTo(0, 99999);
            if (index < message.length - 1) {
                index++
                step()
            }
        }, 40)
    }
    step()
})